var editMode = 0; // 0: non editable, 1: edit hours, 2: edit minutes
var currentTime = new Date();
var lastUpdateTime = currentTime.getTime();
function padLeft(value, length) {
    var strValue = value.toString();
    while (strValue.length < length) {
        strValue = '0' + strValue;
    }
    return strValue;
}
function updateClock() {
    var clockElement = document.getElementById('clock');
    if (clockElement) {
        var hours = padLeft(currentTime.getHours(), 2);
        var minutes = padLeft(currentTime.getMinutes(), 2);
        var seconds = padLeft(currentTime.getSeconds(), 2);
        clockElement.textContent = "".concat(hours, ":").concat(minutes, ":").concat(seconds);
    }
    console.log("Clock updated: ".concat(clockElement === null || clockElement === void 0 ? void 0 : clockElement.textContent));
}
function toggleMode() {
    editMode = (editMode + 1) % 3;
    var modeButton = document.getElementById('mode');
    if (modeButton) {
        if (editMode === 0) {
            modeButton.textContent = 'Mode: View';
        }
        else if (editMode === 1) {
            modeButton.textContent = 'Mode: Edit Hours';
        }
        else if (editMode === 2) {
            modeButton.textContent = 'Mode: Edit Minutes';
        }
    }
    console.log("Mode changed: ".concat(editMode));
}
function increaseTime() {
    if (editMode === 1) {
        currentTime.setHours(currentTime.getHours() + 1);
    }
    else if (editMode === 2) {
        currentTime.setMinutes(currentTime.getMinutes() + 1);
    }
    updateClock();
    console.log("Time increased: ".concat(currentTime));
}
// Exposez les fonctions nécessaires
export { updateClock, toggleMode, increaseTime };
// Met à jour l'horloge chaque seconde
setInterval(function () {
    var now = new Date().getTime();
    var elapsed = now - lastUpdateTime;
    lastUpdateTime = now;
    if (editMode === 0) {
        currentTime = new Date(currentTime.getTime() + elapsed);
    }
    else {
        currentTime.setSeconds(currentTime.getSeconds() + Math.floor(elapsed / 1000));
    }
    updateClock();
}, 1000);
